@import url(https://fonts.googleapis.com/css2?family=Fira+Sans&family=Merriweather&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.App{
    background: linear-gradient(to top,rgb(199, 240, 253),deepSkyBlue, rgb(199, 240, 253), deepSkyBlue);
    padding-top: 0px;
    margin: 0 auto;
    color: rgb(255, 255, 255);
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;    
}

header{
font-family: cursive,sans-serif;
color: yellow;
text-align: center;
height: 14vh;
Font-size: 7vh;
display: flex;
flex-direction: column;
justify-content: space-evenly;
background:linear-gradient(to top,deepSkyBlue,white);
}

.one{
    display: flex;
    justify-content: space-evenly;
    padding: 5px;
    width: 100vw;
}

@media(max-width: 500px){
    .App{
        width:460px;
    }
    header{
        width: 460px;
        align-self: center;
    }
    .one{
        display: flex;
        justify-content: space-evenly;
        flex-wrap: wrap;
        width: 460px;
    }
}

.App-clock{
    color:rgb(255, 0, 0);
    font-size: 2.6vh;
    display: flex;
    border-style:solid;
    height:4vh;
    justify-content: center;
    align-items: center;
    width: 15vh;
    border-color: green;
    border-radius: 12px;
    border-width: 4px;
    background-color: rgb(219, 248, 175);
    margin-top: -8vh;
}

.seconds{
    font-size: 1.7vh;
    padding-left: 1vh;
    margin-top:1vh;
}

.cardTime{
    display: flex;
    flex-direction: column;
    align-items: center;
}


@media(max-width: 380px){
    .App-clock{
        margin-top:-9vh;
        width:20vh;
        height:5vh;
        font-size: 4vh;
    }
    .seconds{
        font-size: 2vh;
    }
}
